import React, { useEffect, useState } from "react";

const GitHubLinkButton = () => {
  const [opacity, setOpacity] = useState(1);

  useEffect(() => {
    const handleScroll = () => {
      const scrollY = window.scrollY;
      const windowHeight = window.innerHeight;
      const documentHeight = document.documentElement.scrollHeight;
      const maxScroll = documentHeight - windowHeight;
      const scrollPercent = maxScroll > 0 ? scrollY / maxScroll : 0;
      
      // Create 100-0-100 pattern: visible at top, disappear in middle, visible at bottom
      let newOpacity;
      if (scrollPercent <= 0.15) {
        // Top 15%: fade from 1 to 0
        newOpacity = 1 - (scrollPercent / 0.15);
      } else if (scrollPercent >= 0.85) {
        // Bottom 15%: fade from 0 to 1
        newOpacity = (scrollPercent - 0.85) / 0.15;
      } else {
        // Middle 70%: completely hidden
        newOpacity = 0;
      }
      
      setOpacity(Math.max(0, Math.min(1, newOpacity)));
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);
  return (
    <div 
      className="hideFromPrint"
      style={{
        position: "fixed",
        top: "28px", // Center aligned with PDF button
        right: "160px", // Positioned to the left of PDF button
        zIndex: 1000,
        opacity: opacity,
        transition: "all 0.2s ease",
      }}
    >
      <a
        href="https://github.com/mrtysn/cv"
        target="_blank"
        rel="noopener noreferrer"
        style={{
          color: "#2185d0",
          cursor: "pointer",
          textDecoration: "none",
        }}
        onMouseEnter={(e) => {
          e.target.style.color = "#1678c2";
        }}
        onMouseLeave={(e) => {
          e.target.style.color = "#2185d0";
        }}
      >
        <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
          <path d="M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.30.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z"/>
        </svg>
      </a>
    </div>
  );
};

export default GitHubLinkButton;